@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap');

:root {
  --white: #ffffff;
  --light-white: #eff2fa;
  --orange: #D68F56;
  --light-orange: #FFF8F0;
  --dark-blue: #3060ff;
  --light-violet: #b1afc1;
  --light-grey: #848484;
  --violet: #363E64;
  --pink: #FF74A7;
  --green: #3DAA81;
  --yellow: #E4FF1A;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Ubuntu', sans-serif;
  background-color: var(--light-white);
}

.home {
  background: var(--light-white);
  display: grid;
  gap: 8px 16px;
  grid-template-columns: 2fr 1fr;
}

.size-1{
  font-size: 30px;
}
.size-2{
  font-size: 24px;
}
.size-3{
  font-size: 18px;
}
.size-4{
  font-size: 16px;
}
.size-5{
  font-size: 14px;
}
.size-6{
  font-size: 12px;
}
.size-7{
  font-size: 10px;
}
.size-8{
  font-size: 8px;
}

.p{
  padding: 2px;
}
.p-1 {
  padding: 4px;
}
.p-2 {
  padding: 8px;
}
.p-3 {
  padding: 12px;
}
.p-4 {
  padding: 16px;
}

.px{
  padding-left: 2px;
  padding-right: 2px;
}
.px-1 {
  padding-left: 4px;
  padding-right: 4px;
}
.px-2 {
  padding-left: 8px;
  padding-right: 8px;
}
.px-3 {
  padding-left: 12px;
  padding-right: 12px;
}
.px-4 {
  padding-left: 16px;
  padding-right: 16px;
}

.py{
  padding-top: 2px;
  padding-bottom: 2px;
}
.py-1 {
  padding-top: 4px;
  padding-bottom: 4px;
}
.py-2 {
  padding-top: 8px;
  padding-bottom: 8px;
}
.py-3 {
  padding-top: 12px;
  padding-bottom: 12px;
}
.py-4 {
  padding-top: 16px;
  padding-bottom: 16px;
}

.m {
  margin: 2px;
}
.m-1 {
  margin: 4px;
}
.m-2 {
  margin: 8px;
}
.m-3 {
  margin: 16px;
}

.my {
  margin-top: 2px;
  margin-bottom: 2px;
}
.my-1 {
  margin-top: 4px;
  margin-bottom: 4px;
}
.my-2 {
  margin-top: 8px;
  margin-bottom: 8px;
}
.my-3 {
  margin-top: 16px;
  margin-bottom: 16px;
}

.mx {
  margin-left: 2px;
  margin-right: 2px;
}
.mx-1 {
  margin-left: 4px;
  margin-right: 4px;
}
.mx-2 {
  margin-left: 8px;
  margin-right: 8px;
}
.mx-3 {
  margin-left: 16px;
  margin-right: 16px;
}

.round-1 {
  border-radius: 4px;
}
.round-2 {
  border-radius: 8px;
}
.border {
  border: 1px solid var(--light-grey);
}

.blue {
  color: var(--dark-blue);
}
.violet {
  color: var(--violet);
}
.green {
  color: var(--green);
}
.orange {
  color: var(--orange);
}
.grey {
  color: var(--light-grey);
}
.white {
  color: var(--white);
}
.light-white {
  color: var(--light-white);
}
.yellow {
  color: var(--yellow);
}
.pink {
  color: var(--pink);
}

.bg-blue {
  background: rgb(74,123,255);
  background: radial-gradient(circle, rgba(74,123,255,1) 0%, rgba(48,96,255,1) 100%);
}
.bg-white {
  background-color: var(--white);
}
.bg-light-orange {
  background-color: var(--light-orange);
}
.bg-light-white {
  background-color: var(--light-white);
}
.bg-light-violet {
  background-color: var(--light-violet);
}
.bg-pink {
  background: rgb(254,143,182);
  background: radial-gradient(circle, rgba(254,143,182,1) 0%, rgba(255,116,167,1) 100%);
}

.shadow-2 {
  box-shadow: 0px 0px 22px -6px #707C97;
}

.shadow-1 {
  box-shadow: 0px 0px 20px -8px #808080b9;
}

.flex {
  display: flex;
}
.bold{
  font-weight: bold;
}
.semi-bold{
  font-weight:500;
}
.header {
  grid-column-start: 1;
  grid-column-end: 3;
  justify-content: space-between;
}

.location {
  /* align-self: end; */
}

.aqi-head {
  justify-content: space-between;
}
.greeting {
  margin-top: 8px;
  margin-bottom: 0px;
  padding-bottom: 2px;
}

.info {
  margin-left: auto;
}

.days {
  justify-content: space-between;
  flex-direction: row;
  overflow-x: auto;
  grid-column-start: 1;
  grid-column-end: 3;
}

.aqi-sec {
  grid-area: 'aqi-sec';
}
.rise-set {
  justify-content: space-between;
}

.hourly {
  grid-column-start: 1;
  grid-column-end: 3;
  text-align: center;
  overflow-x: scroll;
}

.alert {
  align-items: center;
}

.detailside {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px 16px;
}

.day {
  padding: 8px 12px;
  text-align: center;
  cursor: pointer;
}

.pollutant {
  background-color: var(--light-white);
  margin: 0px 4px;
  text-align: center;
}

.tempSide {
  flex-direction: column;
}

.searchbar {
  align-items: center;
}

.searchbar button {
  outline: none;
  border: none;
  padding: 0;
}

.searchbar input {
  outline: none;
  border: none;
  width: 662px;
  min-width: 170px;
}

.temp-card {
  flex-direction: column;
  align-items: center;
}

.temp-card .temp {
  font-size: 60px;
}


.separator {
  height: 18px;
  border-left: 1px solid var(--white);
}

.fields {
  justify-content: space-evenly;
  font-size: 14px;
}

.name {
  min-width: 90px;
}

.value {
  min-width: 60px;
  text-align: end;
}

.footer {
  justify-content: space-between;
}

.det-search {
  margin: 0;
  padding: 0;
  display: none;
}

.det-search .searchbar {
  margin: 4px;
}
.det-search .searchbar, .det-search input, .det-search button {
  background-color: var(--white);
}

/* scrollbar customization  */
body {
  scrollbar-width: thin;
  scrollbar-color: #C1C1C1 #F1F1F1;
}

/* WebKit and Chromiums */
.hourly::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  background-color: #F1F1F1;
}

.hourly::-webkit-scrollbar-thumb {
  background: #C1C1C1;
  border-radius: 5px;
}


/* responsive */

@media screen and (max-width : 1024px) {
  .home {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width : 768px) {
  .home {
    display: flex;
    flex-direction: column;
  }
  .detailside {
    display: flex;
    flex-direction: column;
  }
  .det-search {
    display: block;
  }
  .temp-search .searchbar {display: none;}
  .aqi-gases {overflow-x: auto;}
}