@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;700&family=Comfortaa:wght@300;400&display=swap');


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Baloo 2', cursive;
  font-family: 'Comfortaa', cursive;
}

/* Location button  */

.give-location {
  text-align: center;
}

.btn-container-off {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: var(--light-white);
  width: 130px;
  height: 50px;
  border: 3px solid var(--violet); 
  border-radius: 28px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.small-btn {
  width: 80px;
  height: 36px;
  border-radius: 20px;
  background-color: var(--violet);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 4px;
  left: 4px;
  cursor: pointer;
}

.light {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: var(--light-grey);
  position: absolute;
  right: 10px;
  top: 15px;
}

.btn-container, .small-btn ,.light {
  -webkit-transition: all 200ms linear;
  -moz-transition: all 200ms linear;
  -o-transition: all 200ms linear;
  -ms-transition: all 200ms linear;
  transition: all 200ms linear;
}

.switch-off {
  color: var(--light-white);
  font-size: .7rem;
  letter-spacing: 2px;
  margin: 0;
}

.btn-container-on {
  background-color: var(--pink);
  background-color: #87fab3e7;
}

.btn-container-on .small-btn {
  left: 40px;
  background-color: var(--violet);
}
.btn-container-on .switch-off{
  color: white;
}

.btn-container-on .light {
  background-color: #d1f4ff;
  /* background-color: var(--white); */
}

/* Loader  */
.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: var(--light-white);
}

.pendulum {
  position: absolute;
  width: 60px;
  height: 60px;
  background: transparent;
  border-radius: 50%;
  animation: rotate 2s -1s ease-out forwards reverse infinite;
}

.first {
  background: var(--white);
  /* box-shadow: 0px 0px 16px 8px rgb(30 30 30 / 61%); */
}

.second {
  animation-delay: 0s;
}

.pendulum::before {
  content: "";
  width: 2px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0%);
  background: var(--green);
  z-index: 2;
}

.pendulum::after {
  content: "";
  width: 12px;
  height: 12px;
  background: var(--green);
  border-radius: 50%;
  position: absolute;
  top: 60px;
  left: 24px;
  z-index: 2;
}

.wait {
  position: relative;
  top: 60px;
  letter-spacing: 1px;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  50% {
    transform: rotate(350deg);
  }

  100% {
    transform: rotate(360deg);
  }
}