}


.separator {
  height: 18px;
  border-left: 1px solid #ffffff;
  border-left: 1px solid var(--white);
}

.fields {
  justify-content: space-evenly;
  font-size: 14px;
}

.name {
  min-width: 90px;
}

.value {
  min-width: 60px;
  text-align: end;
}

.footer {
  justify-content: space-between;
}

.det-search {
  margin: 0;
  padding: 0;
  display: none;
}

.det-search .searchbar {
  margin: 4px;
}
.det-search .searchbar, .det-search input, .det-search button {
  background-color: #ffffff;
  background-color: var(--white);
}

/* scrollbar customization  */
body {
  scrollbar-width: thin;
  scrollbar-color: #C1C1C1 #F1F1F1;
}

/* WebKit and Chromiums */
.hourly::-webkit-scrollbar {
  width: 4px;
  height: 4px;
  background-color: #F1F1F1;
}

.hourly::-webkit-scrollbar-thumb {
  background: #C1C1C1;
  border-radius: 5px;
}


/* responsive */

@media screen and (max-width : 1024px) {
  .home {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width : 768px) {
  .home {
    display: flex;
    flex-direction: column;
  }
  .detailside {
    display: flex;
    flex-direction: column;
  }
  .det-search {
    display: block;
  }
  .temp-search .searchbar {display: none;}
  .aqi-gases {overflow-x: auto;}
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Baloo 2', cursive;
  font-family: 'Comfortaa', cursive;
}

/* Location button  */

.give-location {
  text-align: center;
}

.btn-container-off {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: var(--light-white);
  width: 130px;
  height: 50px;
  border: 3px solid var(--violet); 
  border-radius: 28px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.small-btn {
  width: 80px;
  height: 36px;
  border-radius: 20px;
  background-color: var(--violet);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 4px;
  left: 4px;
  cursor: pointer;
}

.light {
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background: var(--light-grey);
  position: absolute;
  right: 10px;
  top: 15px;
}

.btn-container, .small-btn ,.light {
  transition: all 200ms linear;
}

.switch-off {
  color: var(--light-white);
  font-size: .7rem;
  letter-spacing: 2px;
  margin: 0;
}

.btn-container-on {
  background-color: var(--pink);
  background-color: #87fab3e7;
}

.btn-container-on .small-btn {
  left: 40px;
  background-color: var(--violet);
}
.btn-container-on .switch-off{
  color: white;
}

.btn-container-on .light {
  background-color: #d1f4ff;
  /* background-color: var(--white); */
}

/* Loader  */
.container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: var(--light-white);
}

.pendulum {
  position: absolute;
  width: 60px;
  height: 60px;
  background: transparent;
  border-radius: 50%;
  animation: rotate 2s -1s ease-out forwards reverse infinite;
}

.first {
  background: var(--white);
  /* box-shadow: 0px 0px 16px 8px rgb(30 30 30 / 61%); */
}

.second {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.pendulum::before {
  content: "";
  width: 2px;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, 0%);
          transform: translate(-50%, 0%);
  background: var(--green);
  z-index: 2;
}

.pendulum::after {
  content: "";
  width: 12px;
  height: 12px;
  background: var(--green);
  border-radius: 50%;
  position: absolute;
  top: 60px;
  left: 24px;
  z-index: 2;
}

.wait {
  position: relative;
  top: 60px;
  letter-spacing: 1px;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    -webkit-transform: rotate(350deg);
            transform: rotate(350deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  50% {
    -webkit-transform: rotate(350deg);
            transform: rotate(350deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
